import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { actionUserInfo } from '../actions';

import login from '../image/Login.png';
import wallet from '../image/wallet.png';
import arrow from '../image/arrow.svg';

import './Login.css';

class Login extends React.Component {
  constructor() {
    super();
    this.state = {
      email: '',
      password: '',
      valid: false,
    };
    this.validLogin = this.validLogin.bind(this);
    this.handleForm = this.handleForm.bind(this);
    this.renderButtonLogin = this.renderButtonLogin.bind(this);
  }

  handleForm({ target }) {
    const { name, value } = target;
    this.setState({
      [name]: value,
    });
    this.validLogin();
  }

  validLogin() {
    const { email, password } = this.state;
    const REGEX_EMAIL = /^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i;
    const REGEX_PASSWORD = 5;
    const isValidEmail = email.match(REGEX_EMAIL) !== null;
    const isValidPassword = password.length >= REGEX_PASSWORD;
    if (isValidEmail === true && isValidPassword === true) {
      this.setState({
        valid: true,
      });
    }
  }

  renderButtonLogin() {
    const { valid, email } = this.state;
    const { loginWallet } = this.props;

    const disabledButtonLogin = (
      <button
        className="login-button disabled"
        type="button"
        disabled
      >
        <p>Entrar</p>
      </button>);

    const buttonLogin = (
      <button
        className="login-button"
        type="button"
        onClick={ () => loginWallet(email) }
      >
        <p className="text-button">Entrar</p>
        <img className="arrow" src={ arrow } alt="arrow" />
      </button>);

    return valid ? buttonLogin : disabledButtonLogin;
  }

  render() {
    return (
      <div className="container">
        <div className="login-box">
          <div className="logo-box">
            <h1 className="title-logo">TrybeWallet</h1>
            <img className="wallet-logo" src={ wallet } alt="wallet" />
          </div>
          <label className="label" htmlFor="email">
            Email:
            <input
              className="input"
              name="email"
              type="email"
              data-testid="email-input"
              onChange={ this.handleForm }
            />
          </label>
          <label className="label" htmlFor="password">
            Password:
            <input
              className="input"
              name="password"
              type="password"
              data-testid="password-input"
              onChange={ this.handleForm }
            />
          </label>
          <Link to="/carteira">{this.renderButtonLogin()}</Link>
        </div>
        <div className="login-image">
          <img className="login-image" src={ login } alt="login" />
        </div>
      </div>
    );
  }
}

Login.propTypes = {
  loginWallet: PropTypes.func.isRequired,
};

const mapDispatchToProps = (dispatch) => ({
  loginWallet: (email) => dispatch(actionUserInfo(email)),
});

export default connect(null, mapDispatchToProps)(Login);
