import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import walletImg from '../image/wallet.png';

class Header extends React.Component {
  constructor() {
    super();
    this.state = {
      email: '',
    };
    this.setLocal = this.setLocal.bind(this);
  }

  componentDidMount() {
    this.setLocal();
  }

  setLocal() {
    const { user: { email } } = this.props;
    if (email.length > 0) {
      localStorage.setItem('email', email);
    }
    this.setState({
      email: localStorage.getItem('email'),
    });
  }

  render() {
    const { wallet: { expenses } } = this.props;
    const { email } = this.state;
    return (
      <div className="container-header">
        <div className="header-logo-box">
          <h1 className="header-title-logo">TrybeWallet</h1>
          <img className="header-wallet-logo" src={ walletImg } alt="wallet" />
        </div>
        <div className="header-info-box">
          <span
            className="profile-email"
            data-testid="email-field"
          >
            {`E-mail: ${email}`}
          </span>
          <span>Despesa total:</span>
          <span className="total" data-testid="total-field">
            { parseFloat(expenses.reduce((acumulator, currentValue) => acumulator
          + (currentValue.value
          * currentValue.exchangeRates[currentValue.currency].ask), 0))
              .toLocaleString('en', { style: 'currency', currency: 'BRL' })}
          </span>
          <span data-testid="header-currency-field">BRL</span>
        </div>
      </div>

    );
  }
}

Header.propTypes = {
  user: PropTypes.shape({
    email: PropTypes.string,
  }).isRequired,
  wallet: PropTypes.shape({
    expenses: PropTypes.arrayOf(PropTypes.string),
  }).isRequired,
};

const mapStateToProps = ({ user, wallet }) => ({
  user,
  wallet,
});

export default connect(mapStateToProps)(Header);
