import React from 'react';
import { Route, Switch, HashRouter } from 'react-router-dom';
import { Login, Wallet } from './pages';

function App() {
  return (
    <HashRouter basename='/'>
      <Switch>
        <Route exact path="/" component={ Login } />
        <Route path="/carteira" component={ Wallet } />
      </Switch>
    </HashRouter>
  );
}

export default App;
