import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { actionDeletePurchaseItems, actionUpadateMode } from '../actions';

import deleteIcon from '../image/delete.svg';
import editIcon from '../image/edit.svg';

class ExpanseTable extends Component {
  constructor() {
    super();
    this.deletePurchase = this.deletePurchase.bind(this);
  }

  deletePurchase(id) {
    const { expenses, deletePurchase } = this.props;
    const newList = expenses.filter((a) => a.id !== id);
    deletePurchase(newList);
  }

  updateMode(id) {
    const { updateMode } = this.props;
    document.getElementById('valor').value = '';
    updateMode(id);
  }

  render() {
    const { data: {
      value,
      description,
      method,
      tag,
      currency,
      exchangeRates,
      id,
    } } = this.props;
    return (
      <tr>
        <td>{description}</td>
        <td>{tag}</td>
        <td>{method}</td>
        <td>
          {parseFloat(value)
            .toLocaleString('en', { style: 'currency', currency: [currency] })}
        </td>
        <td>{exchangeRates[currency].name.split('/')[0]}</td>
        <td>
          {parseFloat(exchangeRates[currency].ask)
            .toLocaleString('en', { style: 'currency', currency: [currency] })}
        </td>
        <td>
          {parseFloat(exchangeRates[currency].ask * value)
            .toLocaleString('en', { style: 'currency', currency: 'BRL' })}
        </td>
        <td>Real</td>
        <td className="button-cell">
          <button
            data-testid="edit-btn"
            type="button"
            value="editar"
            onClick={ () => this.updateMode(id) }
          >
            <img src={ editIcon } alt="edit" />
          </button>
          <button
            data-testid="delete-btn"
            type="button"
            value="excluir"
            onClick={ () => this.deletePurchase(id) }
          >
            <img src={ deleteIcon } alt="delete" />
          </button>
        </td>
      </tr>
    );
  }
}

ExpanseTable.propTypes = {
  data: PropTypes.shape({
    value: PropTypes.string,
    description: PropTypes.string,
    method: PropTypes.string,
    tag: PropTypes.string,
    currency: PropTypes.string,
    id: PropTypes.number,
    exchangeRates: PropTypes.shape({}),
  }).isRequired,
  expenses: PropTypes.arrayOf(PropTypes.string).isRequired,
  deletePurchase: PropTypes.func.isRequired,
  updateMode: PropTypes.func.isRequired,
};

const mapStateToProps = ({ wallet: { expenses } }) => ({
  expenses,
});

const mapDispatchToProps = (dispatch) => ({
  deletePurchase: (newList) => dispatch(actionDeletePurchaseItems(newList)),
  updateMode: (id) => dispatch(actionUpadateMode(id)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ExpanseTable);
